.banner {
    background-size: contain; /* Adjust the background image size */
    background-repeat: no-repeat; /* Prevent repeating the image */
    background-position: center center; /* Center the background image */
    height: 950px; /* Adjust the container height as needed */
    color: white;
  }

  .banner-resized {
    background-size: 100% 70%; /* Adjust the size percentage as needed */
    width: 100%;
  }


.content{
    align-items: center;
    padding-top: 500px;
    height: 190px;
    padding-left: 15px;
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;

}

.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: black;
    background-color: #e6e6e6;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;

}

.fade_bottom{
    padding: 25rem;
    height: 10rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,61), #111);
}

